<template>
  <AppPopupWrapper :hidden="popupHidden">
    <AppTopPanel header="–î–æ–±–∞–≤–ª–µ–Ω–∏–µ –≥–æ—Ä–æ–¥–∞">
      <template #nav-buttons>
        <AppButtonIcon icon="add" title="–î–æ–±–∞–≤–∏—Ç—å –≥–æ—Ä–æ–¥" @button-click="addCity(addedCity)"/>
        <AppButtonIcon icon="close" title="–ó–∞–∫—Ä—ã—Ç—å –æ–∫–Ω–æ" @button-click="$emit('popup-toggle')"/>
      </template>
    </AppTopPanel>

    <div class="add-city-container">
      <AppLineText
        inputID="input-city"
        label="–ù–∞–∑–≤–∞–Ω–∏–µ: "
        maxLength="20"
        v-model="addedCity.name"
      />

      <AppLineText
        inputID="input-first-note"
        label="–ó–∞–º–µ—Ç–∫–∞: "
        maxLength="30"
        v-model="firstNote"
      />
    </div>
  </AppPopupWrapper>
</template>

<script>
import AppPopupWrapper from '@/components/AppPopupWrapper'
import AppTopPanel from '@/components/AppTopPanel'
import AppButtonIcon from '@/components/AppButtonIcon'
import AppLineText from '@/components/AppLineText'

export default {
  name: 'CityPopupAdd',

  components: { AppPopupWrapper, AppTopPanel, AppButtonIcon, AppLineText },

  props: { popupHidden: Boolean },

  data () {
    return {
      addedCity: {},
      firstNote: '–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'
    }
  },

  created () {
    this.renewAddedData()
  },

  watch: {
    firstNote: function (note) {
      this.addedCity.notes[0] = note
    }
  },

  methods: {
    renewAddedData () {
      this.addedCity = {
        id: Date.now(),
        name: '',
        notes: ['–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'],
        notesCount: '5'
      }

      this.firstNote = '–ì–æ—Ä–æ–¥ –±—ã–ª —Å–æ–∑–¥–∞–Ω'
    },

    addCity (city) {
      this.$emit('add-city', city)
      this.renewAddedData()
    }
  }
}
</script>

